import React from 'react'
import {Link} from 'react-router-dom'
import TwitterEgg from '../media/twitter_egg_2.jpg'
import DefualtProfile from '../media/defualt_1.png'


export const UserPicture = (props) => {
    const {user, hideLink, isProfile} = props
    if (isProfile) {console.log('this is the profile pic')}
    return hideLink ? ( 
        <div className="profile__picture__shell">
            <UserLink user={user} hideLink={hideLink}>
                <div className={!isProfile ? "profile__picture__shell tweet__avatar" : "profile__picture__shell profil_view"}>
                    <img className={!isProfile ? "profile_img" : "profile_img large"} src={DefualtProfile} alt="white egg blue background"/>
                </div>
            </UserLink>
        </div>
    ) : (
        <div className="profile__picture__shell"> 
            <img className="profile_img" src={DefualtProfile} alt="white egg blue background"/>
        </div>
    )
}


export const UserLink = ({ user, children }) => {

    const handleUserLink = (event) => {
        event.preventDefault();
        event.stopPropagation();
        window.location.href = `/profile/${user && user.username}`
    }

    return (
    <>
        <span className="pointer" onClick={handleUserLink}>
            {children}
        </span>
    </>
    )
}

export function UserDisplay(props) {
    const {user, includeFullName, hideLink} = props
    const nameDisplay = includeFullName ? `${user.first_name} ${user.last_name}` : `${user.username}`

    return (
        <div className="user-display__container">
            {nameDisplay}{' '}
            {hideLink ? `@${user.username}` : <UserLink user={user}>{`@${user.username}`}</UserLink>}
        </div>
    )
}

export function UserImages(props) {
    const {user, banner} = props
    return (
        <div className="images__container">
            <div className="profile__banner_picture"><img className="banner-img" src={banner} alt="mountain in front of sunset"/></div>
            <div className="profile__picture">
                <UserPicture user={user} isProfile hideLink />
            </div>
        </div>
        
    )
}



